import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage-angular';
import { BehaviorSubject, from } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Help } from '../models/Help';
import { map, switchMap, tap } from 'rxjs/operators';
import { StorageService } from './storage.service';
import { FormService } from './form.service';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CoordsService {
  public coords = new BehaviorSubject<any>(null);
  public optionChosed: BehaviorSubject<any> = new BehaviorSubject(null);
  private url = environment.url;
  constructor(public  http: HttpClient,
    private storageService: StorageService,
    private formService: FormService) {

  }
   async loadUser(){
   await this.storageService.getStorage2('latlng').then(
    res =>{
    console.log(res);
    }
   );

}
 setCoords(latlng: any){
  console.log(latlng);
      const data = {
        "latitude":latlng[0],
        "longitude":latlng[1],
      };
     const b =  this.storageService.saveStorage('latlng',latlng);
     this.formService.update(latlng,'latlng');
     return this.http.post(`${this.url}/help`,data).pipe(
      switchMap((res: { msg, nameId }) =>{
        const id = this.storageService.setStorage('id',res.nameId);
        this.formService.setId(id);
       return from(id);
      })/* ,
      tap(_ => {
        console.log("datos guardados");
      }) */
    );
}



async getCoords(): Promise<boolean>{
let isData: boolean;
  await this.storageService.hasValue('latlng').then(
    res=>{
        isData = res;
    }
  );
    if(isData){
        this.coords.next(await  this.storageService.getStorage2('latlng'));
        return Promise.resolve(true);
    }
    else{
       return Promise.resolve(false);
    }
}

 getData(){
    return this.coords.asObservable();
}
}
